import React, { Component } from "react";
import "../styles/styles.css";

class Header extends Component {
  state = {};
  render() {
    return (
      <div className="container">
        <header>
          <div id="avatar">
            <img href="/" src="../static/Jesal.jpg" alt="Avatar">
              <Link href="/">
                <a>Home</a>
              </Link>
            </img>
          </div>
        </header>
      </div>
    );
  }
}

export default Header;
